import { useDropdown } from "contexts/dropdown-context";
import React from "react";

const List = ({ children }) => {
  const { show } = useDropdown();
  return (
    <>
      {show && (
        <div className="absolute left-0 z-20 w-full bg-white border border-gray-200 shadow-sm border-t-transparent top-full">
          {children}
        </div>
      )}
    </>
  );
};

export default List;
